.user {
  display: grid;
  grid-template-columns: 70px 1fr;
  column-gap: 9px;

  &__photo {
    width: 70px;
    height: 70px;
    border-radius: 50%;
  }

  &__info {
    color: #4c4b4b;
    font-family: "Source Sans Pro", sans-serif;
    font-size: 14px;
    padding-top: 22px;
    line-height: 18px;

    &-name {
      color: #000000;
      font-family: Overpass, sans-serif;
      font-size: 22px;
      font-weight: 700;
      line-height: 25px;
      width: 209px;
      //white-space: nowrap;
      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &-email {
      max-width: 209px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &-position {
      margin-top: 11px;
    }
  }
}

