.users {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 131px;
  padding-bottom: 110px;

  &.requestIsFail {
    display: none;
  }

  &__container {
    width: 1140px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    color: #000000;
    text-align: center;
  }

  &__subtitle {
    margin-top: 17px;
    text-align: center;
    color: #000000;
  }

  &__list {
    margin-top: 58px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 29px;
    row-gap: 60px;
  }

  &__button-show-more {
    margin-top: 56px;
  }
}

@media (max-width: 1170px) {
  .users {
    padding-top: 130px;

    &__container {
      width: 929px;
    }

    &__subtitle {
      margin-top: 16px;
      letter-spacing: 0.2px;
      font-family: "PT Sans", sans-serif;
    }

    &__list {
      margin-top: 60px;
      column-gap: 31px;
      row-gap: 64px;
    }

    &__button-show-more {
      margin-top: 55px;
    }
  }
}

@media (max-width: 960px) {
  .users {
    padding-top: 91px;
    padding-bottom: 70px;

    &__container {
      width: 721px;
    }

    &__subtitle {
      margin-top: 17px;
      letter-spacing: 0;
      font-family: "Source Sans Pro", sans-serif;
    }

    &__list {
      margin-top: 40px;
      column-gap: 31px;
      row-gap: 40px;
    }

    &__button-show-more {
      margin-top: 61px;
    }
  }
}

@media (max-width: 755px) {
  .users {
    padding-top: 54px;
    padding-bottom: 30px;

    &__container {
      width: 100vw;
      padding: 0 15px;
      box-sizing: border-box;
    }

    &__subtitle {
      margin-top: 16px;
      width: 160px;
      line-height: 22px;
    }

    &__list {
      grid-template-columns: 1fr;
      row-gap: 29px;
      margin-top: 30px;
    }

    &__button-show-more {
      margin-top: 28px;
    }
  }
}
